<template>
<div>
    <figure class="figure">

    <p class="img-holder">
        <img 
        :src="produto.fotos[0]"
        class="figure-img img-fluid mx-auto"
        :alt="produto.nome">
    </p>

    <figcaption class="figure-caption">
        <p class="produto-fabricante">
            <a 
            v-if="produto.fabricante != null"
            :href="produto.fabricante.url"
            v-html="produto.fabricante.nome"
            ></a>
        </p>

        <p class="produto-nome" v-html="produto.nome"></p>
        <p class="produto-preco text-center">{{makeItReal(produto.precos.preco)}}</p>
    </figcaption>

    </figure>

</div>
</template>

<script>
export default {
    name: 'BlocoProduto',
    props: ['produto'],
    methods: {
        makeItReal: function(num) {
            return 'R$' + num
        }
    }
}
</script>

<style scoped>
  .img-holder{
    height: 15rem;
    position: relative;
    width: 100%;
  }
  .img-holder img{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 100%;
    max-width: 100%;
  }
  figure{
      border: 1px solid #d7d7d7;
      border-top: 2px solid #d7d7d7;
      width: 100%;
      padding: 1rem;
  }
  figcaption p{
      margin: 5px 0;
      color: #222
  }
  .produto-fabricante{
      display: inline-block;
      width: 100%;
      height: 20px;
      text-align: center;
      font-size: 11px;
      text-transform: uppercase;
  }
  .produto-nome{
      height: 40px;
      line-height: 20px;
      overflow: hidden;
      display: inline-block
  }
  .produto-preco{
      display: inline-block;
      height: 30px;
      line-height: 30px;
      width: 100%;
      font-size: 18px;
      font-weight: 500;
  }
</style>


