<template>
<div>
  <b-row>

    <b-col v-for="produto in produtos"
    cols="3"
    class="produto-card"
    v-bind:key="produto.id">

      <BlocoProduto v-bind:produto="produto"></BlocoProduto>

    </b-col>

  </b-row>

</div>
</template>

<script>
import axios from 'axios'
import BlocoProduto from './BlocoProduto.vue'

export default {
  name: 'GrupoProdutos',
  data: function() {
    return {
      produtos: {}
    }
  },
  components: {
    BlocoProduto
  },
  methods: {
    fetchProdutos: function() {
      axios
      .post(
          'http://cintrashop.com.br/api-loja/produtos/home',
          'LOJA=4924&LvToken=0-2018082201380808654895156-1561516516514924-3245698441649242018'
      )
      .then(response => (
        this.produtos = response.data.produtos
      ));
    }
  },
  mounted: function() {
    this.fetchProdutos()
  }
}

</script>


