<template>
<div>
  <b-container>
  <b-row>

    <b-col v-for="produto in produtos"
    cols="3"
    class="produto-card"
    v-bind:key="produto.id">
      
      <BlocoProduto v-bind:produto="produto"></BlocoProduto>

    </b-col>

  </b-row>
  </b-container>

</div>
</template>

<script>
import axios from 'axios'
import BlocoProduto from './BlocoProduto.vue'

export default {
  name: 'Listagem',
  data: function() {
    return {
        produtos: {}
    }
  },
  components: {
    BlocoProduto
  },
  methods: {
    getId: function() {
      return this.params = this.$route.params.id
    },  
    fetchProdutos: function() {
      axios
      .post(
          'http://cintrashop.com.br/api-loja/produtos/listagem',
          'LOJA=4924&LvToken=0-2018082201380808654895156-1561516516514924-3245698441649242018&InfoListagem=_|__|_'+this.getId()+'_|__|__|__|__|_PROD.PROD_NOME_|__|__|__|__|__|_'
      )
      .then(response => (
        this.produtos = response.data.produtos
      ));
    }
  },
  mounted: function() {
    this.fetchProdutos()
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


